@import '../common/styles/sass/variables.scss';
.work {
  background-color: #1f1f20;
  min-height: 100vh;

  .workBlock {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .workTitle {
      /*background-color: white;*/
      color: $textColor;
    }

    .workButton {
      border-radius: 50px;
      height: 50px;
      width: 75px;
      margin: 50px;
      background-color: rgba(48, 37, 64, 0.99);
      color: #4e93e6;
    }
  }
}


